import createProxy from 'next-intl/proxy';

export default createProxy({
  // A list of all locales that are supported
  locales: ['en', 'fr', 'rw'],

  // Used when no locale matches
  defaultLocale: 'en'
});

export const config = {
  // Match only internationalized pathnames
  matcher: ['/', '/(fr|rw|en)/:path*']
};
